<template>
  <div class="notice-write-container">
    <h2 class="title">Í≥µÏßÄÏÇ¨Ìï≠ ÏûëÏÑ±</h2>
<p></p>
    <!-- 401 ÏóêÎü¨ ÌåùÏóÖ -->
    <Popup401 v-if="show401Popup" />

    <div v-if="notice">
      <!-- Ï†úÎ™© ÏûÖÎ†• ÏòÅÏó≠ -->
<!-- Ï†úÎ™© ÏûÖÎ†• ÏòÅÏó≠ -->
<div class="title-container" style="position: relative;">

  <label for="title-input" class="label">Ï†úÎ™©</label>

  <input
    id="title-input"
    v-model="notice.noticeTitle"
    @input="limitTitleLength"
    :class="{ 'input-error': notice.noticeTitle.length >= titleMaxLength }"
    class="title-input"
    placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî. (200Ïûê Ïù¥ÎÇ¥)"
  />

  <div class="title-character-count" :class="{ 'error-text': notice.noticeTitle.length >= titleMaxLength }">
  {{ notice.noticeTitle.length }} / {{ titleMaxLength }}
</div>

<span class="error-text" v-show="titleError">{{ titleError }}</span>

</div>



<div v-if="showSuccessPopup" class="popup-overlay">
  <div class="popup">
    <h2>ÎèôÍµ¨ÎùºÎØ∏</h2>
    <hr />
    <p class="confirm-message">Í≥µÏßÄÏÇ¨Ìï≠Ïù¥ Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Ï†ÄÏû•ÎêòÏóàÏñ¥Ïöî.</p>
    <div class="popup-buttons">
      <button @click="closeSuccessPopup">ÌôïÏù∏</button>
    </div>
  </div>
</div>



<div v-if="showUnexpectedErrorPopup" class="popup-overlay">
      <div class="unexpectedPopup">
        <h2>ÎèôÍµ¨ÎùºÎØ∏</h2>
        <hr />
        <p class="confirm-message">
          <span class="error-highlight">ÏòàÍ∏∞Ïπò Î™ªÌïú Ïò§Î•ò</span>Í∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.<br>Î¨∏Ï†úÍ∞Ä Í≥ÑÏÜçÎê† Ïãú, Í¥ÄÎ¶¨ÏûêÏóêÍ≤å Î¨∏ÏùòÌï¥Ï£ºÏÑ∏Ïöî.</p>
        <div class="unexpectedPopup-buttons">
          <button @click="hideUnexpectedErrorPopup">ÌôïÏù∏</button>
        </div>
      </div>
    </div>


      <!-- ÎÇ¥Ïö© ÏûÖÎ†• ÏòÅÏó≠ -->
      <div class="content-container">
  <div class="textarea-wrapper">
    
    <textarea
  id="content-input"
  v-model="notice.noticeContent"
  @input="limitContentLength"
  :class="{ 'input-error': notice.noticeContent.length >= contentMaxLength }"
  class="content-input"
  placeholder="ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî. ÏÇ¨ÏßÑÏùÄ 5Ïû•ÍπåÏßÄ Ï≤®Î∂Ä Í∞ÄÎä•Ìï©ÎãàÎã§."
></textarea>
<div class="character-count" :class="{ 'error-text': notice.noticeContent.length >= contentMaxLength }">
  {{ notice.noticeContent.length }} / {{ contentMaxLength }}
</div>

    <span v-if="contentError" class="error-text">{{ contentError }}</span>
  </div>
</div>

      <!-- Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú ÏòÅÏó≠ -->
      <div class="image-upload-container">
        <div
          v-for="(image, index) in images"
          :key="index"
          class="image-preview"
        >
          <img :src="image.src" alt="Uploaded Image" class="uploaded-image" />
          <div class="edit-icon" @click="editImage(index)">
            <img src="@/assets/penbrush.png" alt="Edit Icon" />
          </div>
          <div class="delete-icon" @click="deleteImage(index)">
            &times;
          </div>
          <input
            type="file"
            :ref="'fileInput' + index"
            @change="onImageChange(index, $event)"
            style="display: none;"
          />
        </div>

        <!-- ÏµúÎåÄ 5Ïû• Ï†úÌïú -->
        <div v-if="images.length < 5" class="image-upload">
          <input type="file" @change="onImageUpload" />
          <div class="plus" @click="$event.target.previousElementSibling.click()">
            +
          </div>
        </div>
      </div>

      <!-- ÏôÑÎ£å Î≤ÑÌäº -->
      <button class="submit-button" @click="submitNotice" :disabled="isLoading">
        ÏôÑÎ£å
      </button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import store from '@/store/store';
import Popup401 from "@/components/Admin/401Popup.vue";

export default {
  name: 'NoticeWrite',
  components: {
    Popup401,
  },
  props: ['noticeUUID'],
  data() {
    return {
      notice: {
        noticeTitle: '',
        noticeContent: ''
      },
      images: [],             // Ïù¥ÎØ∏ÏßÄ ÌååÏùº Î∞è ÎØ∏Î¶¨Î≥¥Í∏∞ Ï†ÄÏû•
      show401Popup: false,    // 401 ÏóêÎü¨ ÌåùÏóÖ
      showSuccessPopup: false, // Í≥µÏßÄÏÇ¨Ìï≠ ÏûëÏÑ± ÏÑ±Í≥µ ÌåùÏóÖ
      isLoading: false,       // Î°úÎî© ÏÉÅÌÉú (ÌïÑÏöî Ïãú ÏÇ¨Ïö©)
      titleError: '', // Ï†úÎ™© Ïò§Î•ò Î©îÏãúÏßÄ
      contentError: '', // ÎÇ¥Ïö© Ïò§Î•ò Î©îÏãúÏßÄ
      titleMaxLength: 200, // Ï†úÎ™© ÏµúÎåÄ Í∏ÄÏûê Ïàò
      contentMaxLength: 3000, // ÎÇ¥Ïö© ÏµúÎåÄ Í∏ÄÏûê Ïàò
      showUnexpectedErrorPopup: false
    };
  },
  methods: {
    // Í∏ÄÏûêÏàò Ï†úÌïú
    limitTitleLength() {
    if (this.notice.noticeTitle.length > this.titleMaxLength) {
      this.notice.noticeTitle = this.notice.noticeTitle.slice(0, this.titleMaxLength);
    }
  },
  limitContentLength() {
    if (this.notice.noticeContent.length > this.contentMaxLength) {
      this.notice.noticeContent = this.notice.noticeContent.slice(0, this.contentMaxLength);
    }
  },

    // ÏÑ±Í≥µ ÌåùÏóÖ Îã´Í∏∞
    closeSuccessPopup() {
      this.showSuccessPopup = false;
      // ÌåùÏóÖ Îã´ÏùÄ ÌõÑ Í≥µÏßÄÏÇ¨Ìï≠ Î™©Î°ù ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
      this.$router.push({ name: 'Notice' });
    },

    // Ïù¥ÎØ∏ÏßÄ ÏÇ≠Ï†ú
    deleteImage(index) {
      this.images.splice(index, 1);
      this.$refs[`fileInput${index}`] = null; // ÌååÏùº ÏûÖÎ†• ÌïÑÎìú Ï¥àÍ∏∞Ìôî
    },

    // ÏòàÍ∏∞Ïπò Î™ªÌïú Ïò§Î•ò ÌåùÏóÖ Ïà®Í∏∞Í∏∞
    hideUnexpectedErrorPopup() {
      this.showUnexpectedErrorPopup = false;
    },

    // 401 ÏóêÎü¨ Í≥µÌÜµ Ï≤òÎ¶¨
    handle401Error(error) {
      if (error.response && error.response.status === 401) {
        this.show401Popup = true;
        return true;
      }
      return false;
    },

    // Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú
    onImageUpload(event) {
      const file = event.target.files[0];
      if (file) {
        const validExtensions = ['png', 'jpg', 'jpeg'];
        const fileExtension = file.name.split('.').pop().toLowerCase();
        const maxFileSize = 10 * 1024 * 1024;

        if (validExtensions.includes(fileExtension) && file.size <= maxFileSize) {
          const reader = new FileReader();
          reader.onload = (e) => {
            this.images.push({ src: e.target.result, file });
            // ÌååÏùº ÏûÖÎ†• ÌïÑÎìú Ï¥àÍ∏∞Ìôî (Í∞ôÏùÄ ÌååÏùº Îã§Ïãú ÏóÖÎ°úÎìú Í∞ÄÎä•)
            event.target.value = "";
          };
          reader.readAsDataURL(file);
        } else {
          alert('ÌååÏùº ÌòïÏãùÏù¥ ÎßûÏßÄ ÏïäÍ±∞ÎÇò ÌÅ¨Í∏∞Í∞Ä Ï¥àÍ≥ºÎêòÏóàÏäµÎãàÎã§. (10MB Ïù¥Ìïò, png/jpgÎßå ÌóàÏö©)');
        }
      }
    },

    // Ïù¥ÎØ∏ÏßÄ ÏàòÏ†ï ÏïÑÏù¥ÏΩò ÌÅ¥Î¶≠
    editImage(index) {
      this.$refs[`fileInput${index}`][0].click();
    },

    // Ïù¥ÎØ∏ÏßÄ ÏàòÏ†ï Ïã§Ï†ú Ï≤òÎ¶¨
    onImageChange(index, event) {
      if (!event || !event.target || !event.target.files) {
        console.error('Invalid event object:', event);
        return;
      }
      const file = event.target.files[0];
      if (!file) return;

      const validExtensions = ['png', 'jpg', 'jpeg', 'gif', 'bmp', 'webp', 'tiff'];
      const fileExtension = file.name.split('.').pop().toLowerCase();
      const maxFileSize = 10 * 1024 * 1024;

      if (validExtensions.includes(fileExtension) && file.size <= maxFileSize) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.images[index] = {
            ...this.images[index],
            src: e.target.result,
            file
          };
        };
        reader.readAsDataURL(file);
      } else {
        alert('ÌååÏùº ÌòïÏãùÏù¥ ÎßûÏßÄ ÏïäÍ±∞ÎÇò ÌÅ¨Í∏∞Í∞Ä Ï¥àÍ≥ºÎêòÏóàÏäµÎãàÎã§. (10MB Ïù¥Ìïò, png/jpgÎßå ÌóàÏö©)');
      }
    },

    // Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÏ∂ú
    async submitNotice() {
     // const maxTitleLength = 200;
      //const maxContentLength = 3000;

      // üîπ Ï†úÎ™©Í≥º ÎÇ¥Ïö© ÏûÖÎ†• Ïó¨Î∂Ä ÌôïÏù∏ ÌõÑ Ïò§Î•ò Î©îÏãúÏßÄ ÏÑ§Ï†ï
    if (this.notice.noticeTitle.trim() === "") {
        this.titleError = "* Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";
      } else {
        this.titleError = "";
      }

      if (this.notice.noticeContent.trim() === "") {
        this.contentError = "* ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";
      } else {
        this.contentError = "";
      }


      try {
        this.isLoading = true;

        // Ï†ÑÏÜ°Ìï† Îç∞Ïù¥ÌÑ∞ Íµ¨ÏÑ±
        const form = new FormData();
        const noticeData = {
          noticeTitle: this.notice.noticeTitle,
          // Í≥µÎ∞±Í≥º Ï§ÑÎ∞îÍøà Ï≤òÎ¶¨
          noticeContent: this.notice.noticeContent
            .replace(/ /g, '&nbsp;')
            .replace(/\n/g, '<br>'),
          photoOrders: this.images.length > 0
            ? this.images.map((_, index) => index + 1)
            : [],
        };

        // request JSON Î∂ÄÎ∂Ñ
        form.append(
          'request',
          new Blob([JSON.stringify(noticeData)], { type: 'application/json' })
        );

        // ÏÇ¨ÏßÑ(ÌååÏùº)Îì§ Ï∂îÍ∞Ä
        if (this.images.length > 0) {
          this.images.forEach((image) => {
            form.append('photos', image.file);
          });
        }

        // API ÏöîÏ≤≠
        const response = await axios.post(
          `${store.state.apiBaseUrl}/notices`,
          form,
          {
            headers: {
              Authorization: `Bearer ${store.state.accessToken}`,
              'Content-Type': 'multipart/form-data',
            },
          }
        );

        // Ï†ïÏÉÅ Îì±Î°ù Ïãú
        if (response.status === 200) {
          
          // ÌåùÏóÖ ÌëúÏãú
          this.showSuccessPopup = true;
        }

        if (!response || !response.data) {
          console.error('ÏùëÎãµ Îç∞Ïù¥ÌÑ∞ ÏóÜÏùå');
        //  alert('Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÏ∂úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.');
          return;
        }

        // ÏÑúÎ≤Ñ ÏùëÎãµ ÏÉÅÌÉú ÏΩîÎìú ÌôïÏù∏
        if ([401].includes(response.status)) {
          let message = '';

          if (response.status === 401) {
            message = 'Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ ÏÇ¨Ïö©ÏûêÏûÖÎãàÎã§. Îã§Ïãú Î°úÍ∑∏Ïù∏Ìï¥Ï£ºÏÑ∏Ïöî.';
            this.$router.push({ name: 'Login' });
          }
          alert(message);
          return;
        }

        // presignedUrls Ï°¥Ïû¨ Ïãú, Ïã§Ï†ú S3 ÏóÖÎ°úÎìú
        const presignedUrls = response?.data?.data || [];
        if (Array.isArray(presignedUrls) && presignedUrls.length > 0) {
          await Promise.all(
            presignedUrls.map(async (url, index) => {
              const file = this.images[index].file;
              await axios.put(url, file, {
                headers: { 'Content-Type': file.type },
              });
            })
          );
        }

        // (Ïó¨Í∏∞ÏÑú ÌåùÏóÖ ÎùÑÏö∞Îäî Î°úÏßÅÏù¥ Ïù¥ÎØ∏ ÏúÑÏóêÏÑú Ï≤òÎ¶¨Îê®)
        // ÎòêÎäî, Î∞îÎ°ú Ïù¥ÎèôÏãúÌÇ§Í≥† Ïã∂Îã§Î©¥ ÏïÑÎûòÏ≤òÎüº ÏûëÏÑ±:
        // this.showSuccessPopup = true; // -> ÌåùÏóÖ ÌëúÏãú
        // this.$router.push({ name: 'Notice' }); // -> Í≥ßÎ∞îÎ°ú Ïù¥Îèô

      } catch (error) {
        // 401 ÏóêÎü¨ Ï≤òÎ¶¨
        if (error.response && error.response.status === 401) {
          this.show401Popup = true;
          return;
        }

        // 400 ÏóêÎü¨(Ïú†Ìö®ÏÑ± Ïã§Ìå® Îì±)
        // if (error.response && error.response.status === 400) {
        //   const additionalData = error.response.data.additionalData;
        //   if (additionalData.noticeTitle) {
        //    // alert(additionalData.noticeTitle);
        //   }
        //   if (additionalData.noticeContent) {
        //     //alert(additionalData.noticeContent);
        //   }
        //   return;
        // }

        const errorCode = error?.response?.data?.code;

        if (errorCode === 'MAX_UPLOAD_SIZE_EXCEEDED') {
          alert(error.response.data.message);
          return;
        }

        if (errorCode === 'INVALID_TITLE_OR_CONTENT') {
          const additionalData = error.response.data.additionalData || {};
          if (additionalData.noticeTitle) {
            this.titleError = additionalData.noticeTitle;
          }
          if (additionalData.noticeContent) {
            this.contentError = additionalData.noticeContent;
          }
          return;
        }


        // Í∏∞ÌÉÄ ÏóêÎü¨
        console.error('Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÏ∂ú Ïã§Ìå®:', error.response || error.message);
      //  alert('Í≥µÏßÄÏÇ¨Ìï≠ Ï†úÏ∂úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.');
      } finally {
        this.isLoading = false;
      }
    },
  },
};
</script>

